.Students-live-pageshow {
  width: 100%;
  display: block;
  background: #1E1D23;
  min-height: 100vh;
  min-width: 1366px;
}
.Students-live-pageshow .pageshow-pic {
  display: block;
  width: 100%;
  height: 100%;
  padding: 60px 0;
}
.Students-live-pageshow .pageshow-pic .text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
}
.Students-live-pageshow .pageshow-pic .top {
  height: 60px;
  line-height: 60px;
  background: #FFFFFF;
  box-shadow: 0 4px 15px rgba(45, 64, 101, 0.08);
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #2A283D;
  opacity: 0.99;
  position: fixed;
  text-align: center;
  width: 100%;
  display: block;
  z-index: 999;
  top: 0;
}
.Students-live-pageshow .pageshow-pic .top .img {
  position: absolute;
  left: 56px;
  top: 50%;
  width: 20px;
  height: 22px;
  transform: translate(0, -50%);
}
.Students-live-pageshow .pageshow-pic .top .img img {
  display: block;
}
.Students-live-pageshow .pageshow-pic .top .title {
  display: inline-block;
  line-height: 60px;
  height: 60px;
}
.Students-live-pageshow .pageshow-pic .preview-box {
  margin: 40px;
  display: block;
  /*height:100%;*/
  padding-right: 28%;
  position: relative;
}
.Students-live-pageshow .pageshow-pic .preview-box .left {
  width: 100%;
  float: left;
  background: #111111;
  border-radius: 6.14698px;
  padding: 30px 0;
  text-align: center;
  min-height: 500px;
  position: relative;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .video-box {
  margin: 25px 0 25px;
  padding: 0 30px;
  position: absolute;
  bottom: 0;
  width: 100%;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .video-box .row-box {
  display: flex;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .video-box .but-box {
  width: 140px;
  height: 28px;
  background: linear-gradient(90deg, #5B93FF 0%, #00BBF8 100%);
  box-shadow: 0px 4px 8px rgba(37, 136, 191, 0.34);
  border-radius: 5px;
  border: none;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .video-box .but-box span {
  color: #fff;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .video-box .img-icon {
  cursor: pointer;
  display: inline-block;
  margin: 0 5px;
  line-height: 36px;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .video-box .img-icon img {
  width: 28px;
}
.Students-live-pageshow .pageshow-pic .preview-box .left #screen div {
  height: 100%!important;
}
.Students-live-pageshow .pageshow-pic .preview-box .left #screen video {
  position: inherit!important;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .time {
  position: absolute;
  text-align: center;
  width: 100%;
  display: block;
  top: 50%;
  transform: translate(0, -50%);
}
.Students-live-pageshow .pageshow-pic .preview-box .left .time .h3 {
  font-family: Microsoft YaHei;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 120.7%;
  color: #FFFFFF;
  display: block;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .time .h3 span {
  font-family: Microsoft YaHei;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 120.7%;
  color: #FFFFFF;
  display: block;
}
.Students-live-pageshow .pageshow-pic .preview-box .left .time .span {
  font-family: Microsoft YaHei;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 120.7%;
  color: #FFFFFF;
}
.Students-live-pageshow .pageshow-pic .preview-box .left #previewIframe {
  margin: 0 0.5%;
}
.Students-live-pageshow .pageshow-pic .preview-box .left img {
  width: 100%;
  height: 95%;
}
.Students-live-pageshow .pageshow-pic .preview-box .right {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 28%;
  float: right;
  border-radius: 6.14698px;
  text-align: left;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera {
  background: #111;
  border-radius: 6.14698px;
  padding: 25px 18px 15px;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera .Camera-top {
  position: relative;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera .Camera-top .h4 {
  font-family: Microsoft YaHei;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 120.7%;
  color: #FFFFFF;
  border-bottom: 1px solid #343434;
  padding-bottom: 10px;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera .Camera-top .icon {
  position: absolute;
  right: 0;
  top: -8px;
  cursor: pointer;
  z-index: 3;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera .Camera-bottom {
  display: flex;
  justify-content: center ;
  text-align: center;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera .Camera-bottom .img {
  padding: 30px 0 0;
  text-align: center;
  height: 180px;
  width: 98%;
  margin: 0 auto;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera .Camera-bottom .img div {
  width: auto!important;
  height: 100%!important;
  position: initial!important;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera .Camera-bottom .img div video {
  width: 100%!important;
  height: 100%!important;
  position: initial!important;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Camera .Camera-bottom .img img {
  position: relative;
  top: 50%;
  transform: translate(0, -50%);
  height: auto;
  max-width: 100%;
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Chat {
  background: #111;
