}
.Students-live-pageshow .pageshow-pic .preview-box .right .Chat .Chat-left {
  display: block;
  /* 定义滚动条高宽及背景 高宽分别对应横竖滚动条的尺寸 */
  /* 定义滚动条轨道 内阴影+圆角 */
  /* 定义滑块 内阴影+圆角 */
}
.Students-live-pageshow .pageshow-pic .preview-box .right .Chat .Chat-left .ant-row {
